var _self;
export default {

	data() {
		return {
			titleNViewBackground: '',
			swiperCurrent: 0,
			swiperLength: 0,
			carouselList: [],
			goodsList: [],
			newList:[], //最新产品
			bestList:[], //精品
			hotList:[] ,//热销产品
			guessList:[] //猜你喜欢
		};
	},

	onLoad() {
		_self = this;
		_self.loadData();
	},
	methods: {
		/**
		 * 请求静态数据只是为了代码不那么乱
		 * 分次请求未作整合
		 */
		async loadData() {
			const res = await this.$apiRequest({
				url:'indexData'
			});
			_self.carouselList = res.data.carouselList;
			_self.swiperLength = res.data.carouselList.length;
			_self.newList = res.data.newData;
			_self.bestList = res.data.bestData;
			_self.hotList = res.data.hotData;
			_self.guessList = res.data.guessData;
			// //let carouselList = await this.$api.json('carouselList');
			// this.titleNViewBackground = carouselList[0].background;
			// //this.swiperLength = carouselList.length;
			// this.carouselList = carouselList;
			
			let goodsList = await this.$api.json('goodsList');
			this.goodsList = goodsList || [];
		},
		//轮播图切换修改背景色
		swiperChange(e) {
			const index = e.detail.current;
			this.swiperCurrent = index;
			// this.titleNViewBackground = this.carouselList[index].background;
		},
		//详情页
		navToDetailPage(goods_id) {
			//测试数据没有写id，用title代替
			//let id = item.goods_id;
			uni.navigateTo({
				url: `/pages/product/product?id=${goods_id}`
			})
		},
	},

























}
